"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { Star, Clock, RotateCcw, DollarSign } from "lucide-react";
import Image from "next/image";

interface Review {
  name: string;
  rating: number;
  feedback: string;
}

interface PricingPackage {
  title: string;
  price: number;
  description: string;
  deliveryTime: string;
  revisions: number;
}

interface Pricing {
  basic: PricingPackage;
  standard: PricingPackage;
  premium: PricingPackage;
}

interface GigPortfolioCardProps {
  title: string;
  niche: string;
  description: string;
  reviews: Review[];
  pricing: Pricing;
  thumbnails: string[];
  onSave?: () => void;
  onAutoUpload?: () => void;
  onExport?: () => void;
  isLoading?: boolean;
}

export function GigPortfolioCard({
  title,
  niche,
  description,
  reviews,
  pricing,
  thumbnails,
  onSave,
  onAutoUpload,
  onExport,
  isLoading = false
}: GigPortfolioCardProps) {
  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, i) => (
      <Star
        key={i}
        className={`w-4 h-4 ${
          i < rating ? "fill-yellow-400 text-yellow-400" : "text-gray-300"
        }`}
      />
    ));
  };

  const copyToClipboard = () => {
    const gigData = {
      title,
      niche,
      description,
      reviews,
      pricing,
      thumbnails
    };
    navigator.clipboard.writeText(JSON.stringify(gigData, null, 2));
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle className="text-2xl font-bold">{title}</CardTitle>
              <Badge variant="secondary" className="mt-2">
                {niche}
              </Badge>
            </div>
            <div className="flex gap-2">
              {onSave && (
                <Button onClick={onSave} variant="outline" disabled={isLoading}>
                  Save Gig
                </Button>
              )}
              {onAutoUpload && (
                <Button onClick={onAutoUpload} disabled={isLoading}>
                  Auto Upload
                </Button>
              )}
              <Button onClick={copyToClipboard} variant="outline">
                Copy Data
              </Button>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <p className="text-gray-700 leading-relaxed">{description}</p>
        </CardContent>
      </Card>

      {/* Sample Thumbnails */}
      <Card>
        <CardHeader>
          <CardTitle className="text-lg">Sample Work</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {thumbnails.map((thumbnail, index) => (
              <div key={index} className="relative aspect-video rounded-lg overflow-hidden">
                <Image
                  src={thumbnail}
                  alt={`Sample work ${index + 1}`}
                  fill
                  className="object-cover"
                />
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Reviews */}
      <Card>
        <CardHeader>
          <CardTitle className="text-lg">Customer Reviews</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {reviews.map((review, index) => (
              <div key={index} className="border-l-4 border-green-500 pl-4">
                <div className="flex items-center gap-2 mb-2">
                  <span className="font-semibold">{review.name}</span>
                  <div className="flex">{renderStars(review.rating)}</div>
                </div>
                <p className="text-gray-600 text-sm">{review.feedback}</p>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>

      {/* Pricing Packages */}
      <Card>
        <CardHeader>
          <CardTitle className="text-lg">Pricing Packages</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            {Object.entries(pricing).map(([tier, pkg]) => (
              <div
                key={tier}
                className={`border rounded-lg p-6 ${
                  tier === "standard" ? "border-blue-500 bg-blue-50" : "border-gray-200"
                }`}
              >
                <div className="text-center mb-4">
                  <h3 className="font-bold text-lg capitalize">{tier}</h3>
                  <p className="text-sm text-gray-600 mb-2">{pkg.title}</p>
                  <div className="flex items-center justify-center gap-1">
                    <DollarSign className="w-5 h-5" />
                    <span className="text-2xl font-bold">{pkg.price}</span>
                  </div>
                </div>
                
                <Separator className="my-4" />
                
                <div className="space-y-3">
                  <p className="text-sm text-gray-700">{pkg.description}</p>
                  
                  <div className="flex items-center gap-2 text-sm">
                    <Clock className="w-4 h-4 text-gray-500" />
                    <span>{pkg.deliveryTime} delivery</span>
                  </div>
                  
                  <div className="flex items-center gap-2 text-sm">
                    <RotateCcw className="w-4 h-4 text-gray-500" />
                    <span>{pkg.revisions} revision{pkg.revisions !== 1 ? 's' : ''}</span>
                  </div>
                </div>
                
                <Button 
                  className="w-full mt-4" 
                  variant={tier === "standard" ? "default" : "outline"}
                >
                  Select {tier}
                </Button>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
