"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Loader2, Sparkles, Zap, Save } from "lucide-react";
import { GigPortfolioCard } from "@/components/GigPortfolioCard";

interface GigData {
  title: string;
  niche: string;
  description: string;
  reviews: Array<{
    name: string;
    rating: number;
    feedback: string;
  }>;
  pricing: {
    basic: {
      title: string;
      price: number;
      description: string;
      deliveryTime: string;
      revisions: number;
    };
    standard: {
      title: string;
      price: number;
      description: string;
      deliveryTime: string;
      revisions: number;
    };
    premium: {
      title: string;
      price: number;
      description: string;
      deliveryTime: string;
      revisions: number;
    };
  };
  thumbnails: string[];
  generatedAt: string;
}

export default function GigGenerator() {
  const [title, setTitle] = useState("");
  const [niche, setNiche] = useState("");
  const [isGenerating, setIsGenerating] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [isUploading, setIsUploading] = useState(false);
  const [generatedGig, setGeneratedGig] = useState<GigData | null>(null);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  const handleGenerate = async () => {
    if (!title.trim() || !niche.trim()) {
      setError("Please enter both title and niche");
      return;
    }

    setIsGenerating(true);
    setError("");
    setSuccess("");

    try {
      const response = await fetch("/api/generate", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          title: title.trim(),
          niche: niche.trim(),
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Failed to generate gig");
      }

      setGeneratedGig(data.data);
      setSuccess("Gig portfolio generated successfully!");
    } catch (err) {
      setError(err instanceof Error ? err.message : "An error occurred");
    } finally {
      setIsGenerating(false);
    }
  };

  const handleSave = async () => {
    if (!generatedGig) return;

    setIsSaving(true);
    setError("");

    try {
      const response = await fetch("/api/saveGig", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(generatedGig),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Failed to save gig");
      }

      setSuccess("Gig saved successfully!");
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to save gig");
    } finally {
      setIsSaving(false);
    }
  };

  const handleAutoUpload = async () => {
    if (!generatedGig) return;

    setIsUploading(true);
    setError("");

    try {
      const response = await fetch("/api/auto-upload", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          gigData: generatedGig,
        }),
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || "Failed to upload gig");
      }

      setSuccess("Gig upload simulation completed!");
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to upload gig");
    } finally {
      setIsUploading(false);
    }
  };

  const exampleGigs = [
    { title: "Create Excel Dashboards", niche: "Data Analysis" },
    { title: "Design Storytelling Thumbnails", niche: "Graphic Design" },
    { title: "Write SEO Blog Posts", niche: "Content Writing" },
    { title: "Create Social Media Content", niche: "Digital Marketing" },
  ];

  return (
    <div className="space-y-8">
      {/* Hero Section */}
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold text-gray-900">
          FiverrCloneBot
        </h1>
        <p className="text-xl text-gray-600 max-w-3xl mx-auto">
          Generate realistic Fiverr gig portfolios automatically with AI-powered content, 
          reviews, and pricing packages. Create 5-10 professional profiles in minutes.
        </p>
      </div>

      {/* Generator Form */}
      <Card className="max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Sparkles className="w-5 h-5 text-blue-500" />
            Generate Your Gig Portfolio
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="title">Gig Title</Label>
              <Input
                id="title"
                placeholder="e.g., Create Excel Dashboards"
                value={title}
                onChange={(e) => setTitle(e.target.value)}
                disabled={isGenerating}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="niche">Niche</Label>
              <Input
                id="niche"
                placeholder="e.g., Data Analysis"
                value={niche}
                onChange={(e) => setNiche(e.target.value)}
                disabled={isGenerating}
              />
            </div>
          </div>

          {/* Example Gigs */}
          <div className="space-y-2">
            <Label className="text-sm text-gray-600">Quick Examples:</Label>
            <div className="flex flex-wrap gap-2">
              {exampleGigs.map((example, index) => (
                <Button
                  key={index}
                  variant="outline"
                  size="sm"
                  onClick={() => {
                    setTitle(example.title);
                    setNiche(example.niche);
                  }}
                  disabled={isGenerating}
                >
                  {example.title}
                </Button>
              ))}
            </div>
          </div>

          <Button
            onClick={handleGenerate}
            disabled={isGenerating || !title.trim() || !niche.trim()}
            className="w-full"
            size="lg"
          >
            {isGenerating ? (
              <>
                <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                Generating Portfolio...
              </>
            ) : (
              <>
                <Zap className="w-4 h-4 mr-2" />
                Generate Gig Portfolio
              </>
            )}
          </Button>

          {/* Status Messages */}
          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {success && (
            <Alert>
              <AlertDescription className="text-green-700">{success}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>

      {/* Generated Gig Display */}
      {generatedGig && (
        <div className="space-y-4">
          <div className="text-center">
            <h2 className="text-2xl font-bold text-gray-900 mb-2">
              Generated Gig Portfolio
            </h2>
            <p className="text-gray-600">
              Your AI-generated Fiverr gig is ready! Review, save, or upload it.
            </p>
          </div>

          <GigPortfolioCard
            title={generatedGig.title}
            niche={generatedGig.niche}
            description={generatedGig.description}
            reviews={generatedGig.reviews}
            pricing={generatedGig.pricing}
            thumbnails={generatedGig.thumbnails}
            onSave={handleSave}
            onAutoUpload={handleAutoUpload}
            isLoading={isSaving || isUploading}
          />
        </div>
      )}
    </div>
  );
}
